<template>
    <div class="example">
        <h2>Form Example</h2>

        <div style="height:1em;background: #4a90e2;"></div>

        <hr>
        <div>
            <p></p>
            <div style="width:320px;">
                {{formData}}
                <Form @submit="onSubmit"
                    :formData="formData"
                    :rules="rules"
                >
                    <form-item field="selectValue" label="" :label-width="labelWidth">
                        <Select v-model="formData.selectValue" size="large">
                            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                        </Select>
                    </form-item>

                    <form-item field="cascaderValue" label="city" :label-width="labelWidth">
                        <Cascader :source.sync="cascaderSource" v-model="formData.cascaderValue" :load-data="loadData" popoverHeight="200px" size="large"></Cascader>
                    </form-item>

                    <form-item field="d" label="" :label-width="labelWidth">
                        <Datepicker v-model="formData.d" :scope="scope" :style="{width:'200px'}"></Datepicker>
                    </form-item>

                    <form-item field="fileList" label="avatar" :label-width="labelWidth">
                        <Upload accept="image/*" action="http://localhost:3000/upload" name="file" v-model="formData.fileList" method="POST" :parse-response="parseResponse">
                            <Button icon="upload"></Button>
                        </Upload>
                    </form-item>

                    <!--
                        ahhh123 -> errors (2)["~~", ""]
                          ~~
                        Promise.all  Promise.all(p1,p2,p3) ()
                        :rules="[{validator:validate1},{maxLength:7,message:''}]"
                        :rules="{blur:[{required:true,message:''},{validator:validate2}],input:[{minLength:6,message:''},{maxLength:10,message:''}]}"
                    -->
                    <form-item field="username" label="" :label-width="labelWidth">
                        <Input type="text" v-model="formData.username" />
                    </form-item>

                    <form-item field="password" label="" :label-width="labelWidth">
                        <Input type="text" v-model="formData.password" size="large"/>
                    </form-item>

                    <form-item field="profile" label="" :label-width="labelWidth">
                        <Textarea type="text" v-model="formData.profile" size="large" />
                    </form-item>

                    <form-item field="sex" label="sex" :label-width="labelWidth">
                        <radio-group v-model="formData.sex">
                            <radio value="0"></radio>
                            <radio value="1"></radio>
                        </radio-group>
                    </form-item>


                    <form-item field="friends" label="" :label-width="labelWidth">
                        {{formData.friends}}
                        <checkbox-group v-model="formData.friends">
                            <Checkbox value="0"></Checkbox>
                            <Checkbox value="1"></Checkbox>
                            <Checkbox value="2"></Checkbox>
                        </checkbox-group>
                    </form-item>

                    <form-item field="open1" label="1" :label-width="labelWidth">
                        <on-off v-model="formData.open1" size="large" :loading="loading1"></on-off>
                        <Button @click="loading1=!loading1" size="small">loading</Button>
                    </form-item>

                    <form-item field="open2" label="2" :label-width="labelWidth">
                        <on-off v-model="formData.open2" on-text="" off-text="" :loading="loading2"></on-off>
                        <Button @click="loading2=!loading2" size="small">loading</Button>
                    </form-item>


                    <form-item :label-width="labelWidth">
                        <Button tag-type="button"></Button>
                    </form-item>
                </Form>
            </div>
        </div>
    </div>
</template>

<script>
  import Form from './index';
  import FormItem from './FormItem';
  import Input from '../Input';
  import Textarea from '../Textarea';
  import Radio from '../Radio';
  import RadioGroup from '../Radio/RadioGroup';
  import Checkbox from '../Checkbox';
  import CheckboxGroup from '../Checkbox/CheckboxGroup';
  import Button from '../Button';
  import OnOff from '../OnOff';
  import Select from '../Select';
  import Option from '../Select/Option';
  import Cascader from '../Cascader';
  import Datepicker from '../Datepicker';
  import Upload from '../Upload';

  import db from '../db';

  const username = ['ahhh111','ahhh123','ahhh234'];
  const passwd = 123456;

  function ajaxRequest(pid = 0) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        let result = db.filter((item) => item.parent_id === pid);
        result.forEach(node => {
          node.isLeaf = db.filter(item => item.parent_id === node.id).length <= 0;
        });
        resolve(result);
      }, 2000);
    });
  }

  export default {
    name: "Example"
    ,components:{Form,FormItem,Input,Textarea,RadioGroup,Radio,Checkbox,CheckboxGroup,Button,OnOff,Select,Option,Cascader,Datepicker,Upload}
    ,data(){
      return {
        formData:{
          username:''
          ,password:''
          ,profile:''
          ,sex:''
          ,friends:[]
          ,open1:true
          ,open2:false
          ,selectValue:''
          ,cascaderValue:[]
          ,d:new Date()
          ,fileList:[]
        }
        ,rules:{
          username: [{validator:this.validate1,trigger:'blur'},{maxLength:7,message:'',trigger:'input'}]

          ,password:[{required:true,message:'',trigger:'blur'},{validator:this.validate2,trigger:'blur'},{minLength:6,message:'',trigger:'input'},{maxLength:10,message:'',trigger:'input'}]

          ,profile:[{required:true,message:'',trigger:'blur'},{minLength:20,message:'',trigger:'input'},{maxLength:25,message:'',trigger:'input'}]

          ,sex:[{required:true,message:'',trigger:'change'}]

          ,friends:[{required:true,message:'111',trigger:'change'}]

          ,selectValue:[{required:true,message:'222',trigger:'change'}]
          ,cascaderValue:[{required:true,message:'333',trigger:'change'}]
          ,d:[{required:true,message:'444',trigger:'change'}]
          ,fileList:[{required:true,message:'555',trigger:'change'}]
        }
        ,loading1:true
        ,loading2:false
        ,cityList: [
          {
            value: 'New York',
            label: 'New York'
          },
          {
            value: 'London',
            label: 'London'
          },
          {
            value: 'Sydney',
            label: 'Sydney'
          },
          {
            value: 'Ottawa',
            label: 'Ottawa'
          },
          {
            value: 'Paris',
            label: 'Paris'
          },
          {
            value: 'Canberra',
            label: 'Canberra'
          }
        ]
        ,cascaderSource:[]
        ,scope:[new Date(2000,1),new Date(2019,12)]
        ,labelWidth:'80px'
      }
    }
    , created() {
      //
      //
      ajaxRequest(0).then((result) => {
        this.cascaderSource = result;
      });
    }
    ,methods:{
      onSubmit(e){
        console.log('e:',e);
        console.log('')
      }
      ,validate1(value){
        return new Promise((resolve, reject) => {
          setTimeout(() => {
            if (username.includes(value)) {
              reject('~~')
            } else {
              resolve()
            }
          }, 2000);
        });
      }
      ,validate2(value){
        return new Promise((resolve, reject) => {
          setTimeout(() => {
            if (passwd==value) {
              resolve()
            } else {
              reject('~~')
            }
          }, 2000);
        });
      }
      ,loadData(node, callback) {
        let {name, id, parent_id} = node;
        ajaxRequest(id).then(result => {
          if (result.length > 0) callback(result); //this.$emit('update:source', copy);
        });
      }
      ,parseResponse (response) {
        let object = JSON.parse(response)
        let url = `http://127.0.0.1:3000/preview/${object.id}`
        return url
      }
    }
  }
</script>

