<template>
    <div :class="rowClass" :style="rowStyle">
        <slot></slot>
    </div>
</template>

<script>
    import {UI_PREFIX} from "../constant";
    export default {
    name: "Row"
    ,props:{
      gutter:{type:[Number,String]}
      ,align:{type:String
        ,validator(value){return ['left', 'right', 'center'].includes(value);}}
    }
    ,computed:{
      /*
        Row
        margin
        width(border-box)
        padding()padding()
        Rowmargin
        
        
      */
      rowStyle(){
        let {gutter} = this;
        return {marginLeft: -gutter / 2 + 'px', marginRight: -gutter / 2 + 'px'};
      }
      ,rowClass(){
        let {align} = this;
        return [`${UI_PREFIX}row`,align && `align-${align}`];
      }
    }
    ,provide(){
        return {
          gutter:this.gutter
        }
    }
  }
</script>

<style lang="scss">
    @import '../var';
    .#{$ui-prefix}row{
        display:flex;

        flex-wrap:wrap; // flex nowrap

        //margin: 0 -10px; // gutter20

        &.align-left {
            justify-content: flex-start;
        }
        &.align-right{
            justify-content:flex-end;
        }
        &.align-center {
            justify-content: center;
        }
    }
</style>
