import Toast from './index';

let currentToast;

export default {
  install(Vue, options) {
    Vue.prototype.$toast = function (message, toastOptions) {
      if(currentToast) {
        currentToast.close();
      }
      currentToast = createToast({Vue, message, propsData: toastOptions, onClose:()=>{currentToast = null;}});
    }
  }
}

function createToast({Vue, message, propsData, onClose}) {
  var Constructor = Vue.extend(Toast); //  beforeMount (beforeMount)
  let toast = new Constructor({propsData:{createdByPlugin:true,...propsData}});
  toast.$slots.default = [message]; // mount
  toast.$on('close', onClose);
  toast.$mount(); //  beforeMount mounted(socss)
  (propsData.container || document.body).appendChild(toast.$el);
  return toast;
}
