/*
TODO singleField  returnAllErrorsfalse  
promise.all(error)[0]
[{rule1},{rule2}]
*/
import {isEmpty} from "../util";

class Validator {
  constructor(){
    this.latestAsyncValidateList = null;
  }

  static addRule(name,fn){
    if (typeof fn !== 'function') return console.error(`the second parameter of 'Validator.addRule' must be a function!`);
    !Validator.extraRules && (Validator.extraRules = {});
    Validator.extraRules[name] = fn; // Boolean
  }

  validate(formData, _rules, cb, returnAllErrors = false) { // cb null | (returnAllErrorstrue) | String(returnAllErrorsfalse)

    if (this.latestAsyncValidateList && !this.latestAsyncValidateList.expired) { // 
      /*
        (expired)

        
        
        
      */
      this.latestAsyncValidateList.expired = true;
    }

    let rules = []
      , isMultifield = false;

    if(Object.prototype.toString.call(formData)!=='[object Object]'){ // StringDateArrayArraycheckbox
      rules = _rules;
    }else{ // 
      Object.keys(_rules).forEach(fieldName => {
        rules.push(..._rules[fieldName].map(rule => (rule.key = fieldName, rule)));
        isMultifield = true;
        returnAllErrors = true;
      });
    }

    const errors = [];
    let hasAsync = false
      , fieldValue = formData;

    for (let i = 0; i < rules.length; ++i) {
      const rule = rules[i];
      if(isMultifield) fieldValue = formData[rule.key];

      if (rule.validator) {
        /*
          instance.validate(,[{validator:fn,trigger:String}],)
          validatorpromise
          promise String()|Null() 
        */
        const r = rule.validator(fieldValue);
        if (!(r instanceof Promise)) { // String | Null
          if (r !== null) { // error
            if (!returnAllErrors) return cb(r);
            errors.push(isMultifield ? [rule.key, r] : r); // x  promise
          }
        }else{ // promise
          errors.push(isMultifield ? [rule.key, r] : r); // returnAllErrorsfalseerrorsrulespromiseerror
          hasAsync = true;
        }
        continue;
      }

      if(rule.required){
        if(isEmpty(fieldValue)){
          if (!returnAllErrors) return cb(rule.message);
          errors.push(isMultifield ? [rule.key, rule.message] : rule.message);
        }
        else if (Array.isArray(fieldValue) && typeof fieldValue[0] === 'object' && fieldValue[0].size) { // 
          let someOneIsUploading = false;
          for(let i=0;i<fieldValue.length;++i) {
            if (fieldValue[i].status !== 'success') {
              someOneIsUploading = true;
            }
          }
          if(someOneIsUploading){
            if (!returnAllErrors) return cb(rule.message);
            errors.push(isMultifield ? [rule.key, '!'] : '!');
          }
        }
      }

      if (rule.minLength && !isEmpty(fieldValue) && fieldValue.length < rule.minLength ||
        rule.maxLength && !isEmpty(fieldValue) && fieldValue.length > rule.maxLength ||
        rule.pattern && !(rule.pattern.test(fieldValue))) {
        if (!returnAllErrors) return cb(rule.message);
        errors.push(isMultifield ? [rule.key, rule.message] : rule.message);
        continue;
      }

      if(Validator.extraRules){
        const key = Object.keys(rule).filter(item => {return !(item === 'trigger') && !(item === 'message')});
        if (Validator.extraRules[key] && !Validator.extraRules[key](fieldValue, rule[key])) {
          if (!returnAllErrors) return cb(rule.message);
          errors.push(isMultifield ? [rule.key, rule.message] : rule.message);
        }
      }
    }

    if (errors.length === 0) {
      return cb(null);
    }

    if (!hasAsync) { // isMultifield  
      return cb(errors);
    }



    /*
      allPromise.all,all([...])promise
      racepromisesingleField(singleFieldpromise)
      thenreturnpromisepromise

      returnAllErrors
      true:
          errors:array<Promise,String> -> all
      false:
          errors:array<Promise> -> race() race all

      all
      
      errorspromiseerrPromiseList (then return)
      :Promise.all
    */

    const errPromiseList = !isMultifield ?
      errors.map(err => {
        const promise = (err instanceof Promise ? err : Promise.reject(err));
        return promise.then(() => {return null;}, (err) => {return err;});
      })
      :errors.map(([key,x]) => {
        const promise = (x instanceof Promise ? x : Promise.reject(x));
        return promise.then(() => {return [key, null];}, (err) => {return [key, err];});
      });

    this.latestAsyncValidateList = errPromiseList;

    Promise.all(errPromiseList).then(r => { // array  r errPromiseList
      if (!errPromiseList.expired) {
        r = r.filter(item => !isMultifield ? item : item[1]); // promisenull
        if (r.length !== 0) {
          /*
              returnAllErrors = false
            [{validator:fn1},{validator:fn2}] 
          */
          if(!isMultifield){
            returnAllErrors ? cb(r) : cb(r[0]);
          }else{
            const result = {};
            r.map(([key, value]) => {
              result[key] = result[key] || [];
              result[key].push(value);
            });
            cb(result);
          }
        } else {
          cb(null);
        }
      }
    });

  }

}

Validator.extraRules = null;

export default Validator;
