<template>
    <transition :name="transitionName" @enter="enter" @leave="leave" >
        <div :class="siderClass" v-show="visible" ref="sider">
            <slot></slot>
        </div>
    </transition>
</template>

<script>
  import {UI_PREFIX} from "../constant";
  export default {
    name: "Sider"
    ,props:{
      visible:{
        type:Boolean
        ,default:true
      }
      ,width:{
        type:String
      }
      ,inCb:{
        type:Function
      }
      ,outCb:{
        type:Function
      }
    }
    ,computed:{
      siderClass(){
        return `${UI_PREFIX}sider`;
      }
      ,transitionName(){
        return `${this.siderClass}-slide`; // xui-sider-slide
      }
    }
    ,mounted() {
      this.$refs.sider.style.width = this.width;
    }
    ,methods:{
      //beforeEnter transitionenter-active
      enter(el,done){ // .enter-active 
        el.style.marginLeft = `-${this.width}`; //display:none->blocktransition""
        el.getBoundingClientRect(); //
        el.style.marginLeft = '0px';

        el.addEventListener('transitionend', () => {
          // console.log('in:transitionend');
          this.inCb && this.inCb();
          done();//done  afterEnter
        },{once:true});
      }
      ,leave(el,done){
        el.style.marginLeft = `-${this.width}`;

        /*
            done  display:none afterLeave
            ,getBoundingClientRect
            ,display:none
            ,Scroll
            ,displaynone
            ,
            ,resize handler getBoundingClientRect
            ,
            ,done()
          */
        el.addEventListener('transitionend', () => {
          // console.log('out:transitionend');
          this.outCb && this.outCb();
          // done();
        },{once:true});
      }
    }
  }
</script>

<style lang="scss">
    @import '../var';
    .#{$ui-prefix}sider{
        position:relative;
    }

    .#{$ui-prefix}sider-slide-enter-active,.#{$ui-prefix}sider-slide-leave-active{
        transition:margin-left .5s;
    }
</style>
