export default {
  getYearMonthDate(date){
    let year = date.getFullYear();
    let month = date.getMonth();
    let day = date.getDate();
    return [year, month, day];
  }
  ,firstDayOfMonth(date){
    let [year, month] = this.getYearMonthDate(date);
    return new Date(year, month, 1); // day1 day0 month  month
  }
  ,lastDayOfMonth(date){
    let [year, month] = this.getYearMonthDate(date);
    return new Date(year, month+1, 0); // day0
  }
  ,addYear(date,n){
    const [year] = this.getYearMonthDate(date);
    const newYear = year + n;
    const copy = new Date(date);
    copy.setFullYear(newYear);
    return copy;
  }
  ,addMonth(date,n){
    const [,month,] = this.getYearMonthDate(date);
    const newMonth = month + n;
    const copy = new Date(date);
    copy.setMonth(newMonth);
    return copy;
  }
  //[x,y)
  ,range(begin,end){
    const array = [];
    for(let i=begin;i<end;++i){
      array.push(i);
    }
    return array;
  }
  ,pad2(number){
    if(typeof number !== 'number'){
      throw new Error('wrong param');
    }
    return number >= 10 ? '' + number : '0' + number;
  }
}
