<template>
    <div class="example">
        <h2>Button Example</h2>
        <div style="height:1em;background: #4a90e2;"></div>

        <hr>
        <div>
            <p>attrs</p>
            <div>
                <Button icon-position="right" href="http://www.baidu.com" target="_blank">a</Button>
                <Button tag-type="button">Button</Button>
                <Button tag-type="input" type="button" value="typebuttoninput"></Button>
            </div>
        </div>

        <hr>
        <div>
            <p>iconiconPosition</p>
            <div>
                <Button icon="upload">Button</Button>
                <Button icon="upload" icon-position="right">Button</Button>
            </div>
        </div>

        <hr>
        <div>
            <p>loading</p>
            <div>
                <Button :loading="loading" icon="upload" @click="handleClick">Button</Button>loading
            </div>
        </div>

        <hr>
        <div>
            <p></p>
            <div>
                <Button :loading="loading" icon="upload" @click="handleClick">Button</Button>@click
                <Button :loading="loading" icon="upload" @mouseenter="handleClick" @mouseleave="handleClick">Button</Button>@mouseentermouseleave
                <Button :loading="loading" @click.native="abc">Button213</Button>@click.native
            </div>
        </div>

        <hr>
        <div>
            <p>button-groupchildrenButtonwarning</p>
            <button-group size="large">
                <Button icon="upload" :class="'abc'"></Button>
                <Button icon="download" icon-position="right" :class="['abc','bbb']" ></Button>
                <Button icon="download" icon-position="right" :class="'abc'"></Button>
            </button-group>
            <br>
            <button-group size="large">
                <div>button-groupwarning</div>
            </button-group>
        </div>

        <hr>
        <div>
            <p>inline-block</p>
            <div>
                <button-group size="large">
                    <Button icon="upload"></Button>
                    <Button icon="download" icon-position="right"></Button>
                    <Button icon="download" icon-position="right"></Button>
                </button-group>
                <Button icon="loading" size="small">a</Button>
                <Button icon="loading" tag-type="input">input</Button>
                <Button icon="loading" tag-type="button" size="large">button</Button>
            </div>
        </div>
    </div>
</template>

<script>
  import Button from './index';
  import ButtonGroup from './ButtonGroup';
  export default {
    name: "Example"
    ,components:{Button,ButtonGroup}
    ,data(){
      return {
        loading:true
      }
    }
    ,methods:{
      changeLoading(){
        this.loading = !this.loading;
      }
      ,handleClick(){
        this.changeLoading();
      }
      ,abc(e){
        alert('native ');
        console.log('this:', this);
        console.log('e:',e);
        console.log('e.target:', e.target);
      }
    }
  }
</script>

