<template>
    <transition :name="transitionName">
        <div :class="slideItemClass" v-if="visible">
            <slot></slot>
        </div>
    </transition>
</template>

<script>
  import {UI_PREFIX} from "../constant";

  export default {
    name: "SlideItem"
    ,props:{
      name:{
        type:String
        ,required:true
      }
    }
    ,inject:['selfAdaption']
    ,data(){
      return {
        selected:undefined
        ,reverse:false
      }
    }
    ,computed:{
      visible(){
        return this.selected === this.name;
      }
      ,slideItemClass(){
        return [`${UI_PREFIX}slide-item`,{reverse:this.reverse,selfAdaption:this.selfAdaption}];
      }
      ,transitionName(){
        return `${UI_PREFIX}slide`;
      }
    }
  }
</script>

<style lang="scss">
    @import '../var';

    // background
    .#{$ui-prefix}slide-item{
        >img{
            vertical-align: bottom;
        }

        &:not(.selfAdaption){ // selfAdaption
            >img{
                width:100%;
            }
        }
    }

    .#{$ui-prefix}slide-enter-active,.#{$ui-prefix}slide-leave-active{
        transition:all 1s;
    }
    .#{$ui-prefix}slide-leave-active{
        /*
             (block)
            translateX(100%)
            
            
        */
        position:absolute;
        left:0;
        top:0;
        width:100%;
        height:100%;
    }

    //  - > translateX(-100%)
    .#{$ui-prefix}slide-leave-to { // translateX(0)
        transform: translateX(-100%);
    }
    // translateX(100%) - > 
    .#{$ui-prefix}slide-enter { //transitiondisplay:none->block
        transform: translateX(100%);
    }

    // translateX(-100%) - > 
    .#{$ui-prefix}slide-enter.reverse {
        transform: translateX(-100%);
    }
    //  - > translateX(100%)
    .#{$ui-prefix}slide-leave-to.reverse {
        transform: translateX(100%);
    }
</style>
